.header {
    height: 50px;
    width: 100%;

    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
}

.headerInner {
    height: 100%;
    width: 70%;

    background-color: rgb(236, 236, 236);
    border-radius: 10px;box-shadow: 14px 14px 28px rgba(0,0,0,0.25), 10px 10px 10px rgba(0,0,0,0.22);

    padding-left: 30px;
    padding-right: 30px;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.headerIcon {
    font-size: 35px;
    color: black;
}

.headerText {
    font-size: 32px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.headerSingUp {
    height: 30px;
    width: 150px;

    font-size: 13px;
    background-color: rgb(226, 226, 226);

    border-radius: 6px;
    border: 1px solid rgb(110, 110, 110);

    cursor: pointer;
}

.headerSingUp:hover {
    transition-duration: 0.2s;

    background-color: rgb(200, 200, 200);
    color: black;
}

